export default {
    name: 'TreeNodeTitle',
    props: {
        tree: Object,
        node: Object
    },
    render(h) {
        let title = '';
        if (this.tree.$scopedSlots.default){
            title = this.tree.$scopedSlots.default({
                node: this.node
            });
        } else {
            title = this.node.text;
        }
        return h(
            'span',
            {
                'class':'tree-title'
            },
            [
                title
            ]
        )
    }
}