export const LABEL_TEMPLATE = `
<label :class="labelClasses">
    <slot></slot>
</label>
`;
export default {
    name: 'Label',
    template: LABEL_TEMPLATE,
    props: {
        for: String,
        align: {
            type: String,
            default: 'left'
        }
    },
    computed: {
        labelClasses(){
            return 'textbox-label textbox-label-'+this.align;
        }
    },
    mounted(){
        if (this.for){
            let att = document.createAttribute('for');
            att.value = this.for;
            this.$el.setAttributeNode(att);
        }
    }
}